@keyframes loadingSpinAround {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(359deg);
  }
}

.heatbox-header .logo-container > div:first-child {
  width: 70%;
}

.heatbox-header .logo-container .subtitle {
  line-height: 1.4;
}

.heatbox-header .logo-container > div:last-child {
  width: 30%;
}

@media screen and (width <= 990px) {
  .heatbox-sidebar {
    margin-top: 20px;
  }
}

@media screen and (width <= 780px) {
  .heatbox-header .logo-container .title, .heatbox-header .logo-container .subtitle {
    width: 100%;
  }
}

@media screen and (width <= 576px) {
  .heatbox-header .logo-container img {
    width: 80px;
  }
}

.weed-fields {
  position: relative;
}

.weed-field {
  margin-bottom: 15px;
}

.weed-field .wp-picker-input-wrap {
  max-width: 220px;
  transition: max-width .2s;
  display: inline-flex;
  overflow: hidden;
}

.weed-field .wp-picker-input-wrap .color-picker {
  width: 160px !important;
}

.weed-field .wp-picker-input-wrap.hidden {
  max-width: 0;
  display: inline-flex;
}

.weed-inline-fields {
  display: flex;
  position: relative;
}

.weed-inline-fields .weed-field {
  margin-right: 10px;
}

.weed-inline-fields .weed-field:last-child {
  margin-bottom: 0;
  margin-right: 0;
}

.weed-label {
  margin-bottom: 3px;
  display: block;
}

.is-hidden {
  display: none;
}

@media screen and (width <= 320px) {
  .weed-radio-fields .radio-label {
    margin-bottom: 10px;
    display: block;
  }
}

.button.weed-test-email-button {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.weed-test-email-button.is-loading {
  pointer-events: none;
  color: #0000 !important;
}

.weed-test-email-button.is-loading:after {
  content: "";
  border-style: solid;
  border-width: 2px;
  border-radius: 290486px;
  width: 1em;
  height: 1em;
  animation: .5s linear infinite loadingSpinAround;
  display: block;
  position: absolute;
  border-color: #0000 #0000 #fff #fff !important;
}

.weed-buttons .weed-reset-button {
  color: #fff;
  text-align: center;
  background-color: tomato;
  border-color: tomato;
  margin-left: 7px;
}

.weed-buttons .weed-reset-button:hover, .weed-buttons .weed-reset-button:active {
  color: #fff;
  background-color: #f5492a;
  border-color: #f5492a;
}

.weed-buttons .weed-reset-button:focus {
  color: #fff;
  background-color: #f5492a;
  border-color: #f5492a;
  box-shadow: 0 0 0 1px #fff, 0 0 0 3px #f5492a;
}

@media screen and (width <= 320px) {
  .weed-buttons .button {
    width: 100%;
    display: block;
  }

  .weed-buttons .weed-reset-button {
    margin-top: 10px;
    margin-left: 0;
  }
}

.CodeMirror-wrap {
  border: 1px solid #ddd;
}

.weed-test-smtp-metabox th {
  display: none;
}

.weed-test-smtp-metabox .form-table td p {
  margin-bottom: 20px;
}

.weed-submission-notice {
  border-style: solid;
  border-width: 2px;
  margin-bottom: 15px;
  padding: 5px 10px;
  font-weight: 700;
}

.weed-submission-notice.is-error {
  color: tomato;
  background-color: #ff63471a;
  border-color: tomato;
}

.weed-submission-notice.is-success {
  color: #4fe190;
  background-color: #4fe1901a;
  border-color: #4fe190;
}

.weed-featured-products > h2 {
  text-align: center;
  margin-bottom: 50px;
  font-size: 24px;
}

@media screen and (width <= 576px) {
  .weed-featured-products > h2 {
    line-height: 1.4;
  }
}

.weed-featured-products .products {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.weed-featured-products .products li {
  width: 32%;
}

@media screen and (width <= 780px) {
  .weed-featured-products .products li {
    width: 100%;
  }
}

.weed-featured-products .products .subheadline {
  margin-top: -6px;
  font-weight: 600;
}

.weed-featured-products .products img {
  max-width: 100%;
}

.weed-featured-products .credit {
  text-align: center;
  opacity: .5;
  margin-top: 20px;
}

.weed-resources-metabox ul {
  margin: 0;
}

.weed-resources-metabox li {
  margin-bottom: 15px;
}

.weed-resources-metabox li:last-child {
  margin-bottom: 0;
}

.weed-resources-metabox a {
  text-decoration: none;
}
